
.scangif {
    width: 40%;
    filter: drop-shadow(4px 8px 4px rgba(0, 0, 0, 0.72));
}

@media screen and ( max-width : 820px) and (min-width : 451px) {
    .scangif {
        width: 60%;
        margin-bottom: 3rem;
       
    }
}

@media screen and ( max-width : 450px ) {
    .scangif {
        width: 65%;
        margin-bottom: .5rem;
       
    }
}
